export const addOrder = async (body) => {
  const response = await fetch("/api/order/checkout", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(body),
  });

  if (!response.ok) {
    throw new Error("ÈÄÅÂá∫Ë®ÇÂñÆÂ§±Êïó");
  }

  return response.json();
};

